<template>
  <v-container fluid>
    <v-row>
      <v-col
        v-for="(stream, name) in streamsSummary"
        :key="name"
        cols="12"
        md="6"
        lg="4"
      >
        <v-card class="pa-4" outlined elevation="6" hover>
          <v-card-title class="text-h6 font-weight-bold">
            {{ name }}
          </v-card-title>

          <v-card-subtitle class="mb-2">
            <v-icon left color="primary">mdi-account-group</v-icon>
            {{ stream.count }} Students
          </v-card-subtitle>

          <v-row class="mb-2">
            <v-col cols="4">
              <div class="text-caption">Required</div>
              <div class="font-weight-bold text-primary">
                Tsh {{ format(stream.required) }}
              </div>
            </v-col>
            <v-col cols="4">
              <div class="text-caption">Paid</div>
              <div class="font-weight-bold text-success">
                Tsh {{ format(stream.paid) }}
              </div>
            </v-col>
            <v-col cols="4">
              <div class="text-caption">Unpaid</div>
              <div class="font-weight-bold text-error">
                Tsh {{ format(stream.unpaid) }}
              </div>
            </v-col>
          </v-row>

          <v-progress-linear
            :value="getPercentage(stream.paid, stream.required)"
            color="success"
            height="8"
            rounded
            striped
            class="mb-1"
          ></v-progress-linear>
          <div class="text-caption text-right">
            {{ getPercentage(stream.paid, stream.required).toFixed(1) }}% Paid
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: ["streamsSummary"],
  methods: {
    getPercentage(paid, required) {
      return (paid / required) * 100;
    },
    format(value) {
      return value.toLocaleString();
    },
  },
};
</script>
